<template>
  <div class="guizi">
    <h2>{{msg}}</h2>
    <button class="Button" :style="{marginLeft: Btnleft,padding:Pad}" v-for="(item,index) in items" :key = "index" @click="sub(item)">{{item.CabinetName}}</button>
    <!--<router-link to="/Gezi/1234">-->
      <!--<button :style="{marginLeft: Btnleft,padding:Pad}" @click="sub()">{{b1}}</button>-->
    <!--</router-link>-->
    <button :style="retu" class="Button" @click="routerback()">{{b2}}</button>

  </div>

  <!--</router-link>-->
</template>

<script>
    export default {
        name: "guizi",
        data(){
            return {
              msg:"请选择柜子",
              b1:"A1",
              b2:"返回",
              retu:{
                position: "absolute",
                bottom: "3%",
                right: "3%"
              },
              items:[],
              Btnleft:"1em",
              Pad:"10px 20px"
            }
        },
        mounted:function () {
          let Url=localStorage.getItem("Url");
          let $this=this;
          this.$indicator.open({
            text: 'Loading...',
            spinnerType: 'fading-circle'
          });
          this.$http({
            method:'post',
            headers: {'Accept': '*/*'},
            url:Url+'/api/ToolClient/QueryListCabinetName',
            data:{
              ID:localStorage.getItem("codeId")
            }
          }).then(function(res){
            $this.$indicator.close();
            if(res.data.Result===200){
              $this.items=res.data.Data;
            }
            console.log(res);
          }).catch(function(err){
            console.log(err)
          })
        },
        methods:{
          routerback(){
            if (this.$route.query.goindex === 'true') {
              this.$router.push('/')
            } else {
              this.$router.back(-1)
            }
          },
          sub(item){
            console.log(item.CabinetName);
            this.$router.push({path: '/Gezi/CabinetName', query: {CabinetName: item.CabinetName}})
          }
        }
    }
</script>

<style scoped>
  .guizi{
    text-align: left;
  }
  h2{
    margin-top: 3%;
    color: red;
    text-indent: 2em;
  }

</style>



