<template>
<div class="jyzc">
    <input :style="inSty"/>
    <button :style="btnSty" class="iconfont icon-sousuo Button"></button>
  <br/>
  <div class="main">
    <table class="tab">
      <thead>
        <td>&nbsp;&nbsp;</td>
        <td>操作</td>
        <td>借用资产</td>
        <td>型号</td>
        <td>货位号</td>
        <td>库存数</td>
      </thead>
      <tr v-for="(item,index) in items">
        <td>&nbsp;&nbsp;{{index+1}}&nbsp;&nbsp;</td>
        <!--:disabled="item.Count<=0"-->
        <td><button class="Button" @click="tool(item)">借用</button></td>
        <td>{{item.ToolName}}</td>
        <td>{{item.Spec}}</td>
        <td>{{item.LocationCode}}</td>
        <td>{{item.Count}}</td>
      </tr>
    </table>
  </div>
  <button class="Button" :style="retu" @click="routerback()">{{b2}}</button>

  <v-dialog v-show="showDialog" :dialog-option="dialogOption" ref="dialog"></v-dialog>
</div>
</template>

<script>
  import dialog from "./dialog"
    export default {
        name: "jyzc",
        data(){
            return{
              b2:"返回",
              items:[],
              retu:{
                position: "absolute",
                  bottom: "3%",
                  right: "3%",
                  padding:" 6px 40px",
                fontSize:"40px"
              },
              inSty:{
                position:"relative",
                width:"90%",
                float:"left",
                marginTop:"-500",
                height:"30px"
              },
              btnSty:{
                width:"8%",
                float:"right",
                marginRight:"1%",
                padding:" 10px 5px",
                position: "relative"
              },
              showDialog:"",
              dialogOption:{
                title:"",
                text:"",
                cancelButtonText:"再次盘库"
              }
            }
        },
        mounted:function () {
          let Url=localStorage.getItem("Url");
          console.log(this.$route.query.dataObj);
          let dataObj=this.$route.query.dataObj;
          let $this=this;
          this.$indicator.open();
          this.$http({
            method:'post',
            headers: {'Accept': '*/*'},
            url:Url+'/api/ToolClient/QueryTotals',
            data:{
              ID:localStorage.getItem("codeId"),
              CabinetName:dataObj.CabinetName,
              BoardName:dataObj.BoardName
            }
          }).then(function(res){
            $this.$indicator.close();
            if(res.data.Result===200){
              $this.items=res.data.Data;

            }
            console.log(res);
          }).catch(function(err){
            console.log(err)
          })
        },
        components:{
          'v-dialog': dialog,
        },
        methods:{
          routerback(){
            if (this.$route.query.goindex === 'true') {
              this.$router.push('/')
            } else {
              this.$router.back(-1)
            }
          },
          tool(item){
            let $this=this;
            let dataObj=this.$route.query.dataObj;
            // setTimeout(()=>{
            // plus.nativeUI.showWaiting( "等待中..." );
            plus.plugintest.PluginTestFunction("Html5","Plus","AsyncFunction",dataObj.BoardName, function( result ) {
              if(result[3]===1){
                // plus.nativeUI.toast("柜门已开");
                plus.nativeUI.showWaiting( "等待中..." );
                run();
                function run() {
                  plus.plugintest.CloseFunction("Html5", "Plus", "AsyncFunction", "MultiArgument!", function (res) {
                    plus.nativeUI.closeWaiting();
                    item["SyCounts"] = res.length;
                    $this.dialogOption.text = item;
                    $this.$indicator.close();
                    $this.showDialog = true;
                    $this.$refs.dialog.confirm().then(() => {
                      $this.showDialog = false;//确定
                      $this.$options.methods.submit(res);
                    }).catch(() => {//再次盘库
                      $this.$options.methods.again(item);
                    })
                  }, function (result) {
                    alert(result)
                  });
                }
                // setTimeout(()=>{
                //   $this.$options.methods.Close();
                // },100)
                // plus.plugintest.CloseFunction("Html5","Plus","AsyncFunction","MultiArgument!", function( result1 ) {
                //   if(result1[3]===4){
                //     plus.plugintest.CountingFunction("Html5", "Plus", "AsyncFunction", "MultiArgument!", function (res) {
                //       plus.nativeUI.showWaiting( "等待中..." );
                //       item["SyCounts"]=res.length;
                //       $this.dialogOption.text=item;
                //       plus.nativeUI.closeWaiting();
                //       $this.showDialog = true;
                //       $this.$refs.dialog.confirm().then(() => {
                //         $this.showDialog = false;//确定
                //       }).catch(() => {//再次盘库
                //         plus.nativeUI.showWaiting( "等待中..." );
                //         plus.plugintest.CountingFunction("Html5", "Plus", "AsyncFunction", "MultiArgument!", function (result2) {
                //           plus.nativeUI.closeWaiting();
                //           alert(result2);
                //         }, function (result) {alert(result)});
                //       })
                //     }, function (result) {alert(result)});
                //   }
                // }, function(result){alert(result)});
                // function Toast(msg,duration){
                //   duration=isNaN(duration)?3000:duration;
                //   var m = document.createElement('div');
                //   m.innerHTML = msg;
                //   m.style.cssText="width: 60%;min-width: 150px;opacity: 0.7;height: 30px;color: rgb(255, 255, 255);line-height: 30px;text-align: center;border-radius: 5px;position: fixed;top: 40%;left: 20%;z-index: 999999;background: rgb(0, 0, 0);font-size: 12px;";
                //   document.body.appendChild(m);
                //   setTimeout(function() {
                //     var d = 0.5;
                //     m.style.webkitTransition = '-webkit-transform ' + d + 's ease-in, opacity ' + d + 's ease-in';
                //     m.style.opacity = '0';
                //     setTimeout(function() { document.body.removeChild(m) }, d * 1000);
                //     }, duration);
                // }
              }
              else if(result[3]===2){
                plus.nativeUI.toast('柜门打开失败');
              }
            }, function(result){alert(result)});
            // },100)
            // .confirm({
            //   title: 'Notice',
            //   message: 'Are you sure?',
            //   showCancelButton: true,
            //   cancelButtonText:"重新盘库"
            // });
            // $this.$messagebox.confirm(html).then(_ => {

              // alert("asfas")

            // }).catch(_ => {

              // alert("45165")

            // })
            // if(item.Count>0){

            // }else{
            //   $this.$toast({
            //     message: '库存为空',
            //     position: 'bottom',
            //     duration: 1000
            //   });
            // }

            // $this.showDialog = false;
            //   $this.$indicator.open({
            //     text: 'Loading...',
            //     spinnerType: 'fading-circle'
            //   });
            //   $this.$http({
            //     method:'post',
            //     headers: {'Accept': '*/*'},
            //     url:'/api/ToolClient/QueryLendList',
            //     data:{
            //       CabinetID:localStorage.getItem("codeId"),
            //       epcs:item.Spec,
            //       UserID:localStorage.getItem("UserID"),
            //       Name:localStorage.getItem("Name")
            //     }
            //   }).then(function(res){
            //     $this.$indicator.close();
            //     // if(res.data.Result===200){
            //     //   $this.items=res.data.Data;
            //     // }
            //     console.log(res);
            //   }).catch(function(err){
            //     console.log(err)
            //   })
            // next();
          },
          Close(){
            let $this=this;
            plus.plugintest.CloseFunction("Html5","Plus","AsyncFunction","MultiArgument!", function( res ) {
              $this.showDialog = true;
              $this.$refs.dialog.confirm().then(() => {
                $this.showDialog = false;//确定
              }).catch(() => {//再次盘库
                $this.$indicator.open();
              })
            }, function (result) {alert(result)});
          },
          Counting(){
            let $this=this;
            $this.showDialog = true;
            plus.plugintest.CountingFunction("Html5", "Plus", "AsyncFunction", "MultiArgument!", function (result1) {
              alert(result1);
            }, function (result) {alert(result)});
          },
          again(item){
            let $this=this;
            plus.plugintest.CountingFunction("Html5", "Plus", "AsyncFunction", "MultiArgument!", function (result2) {
              item["SyCounts"]=res.length;
              $this.dialogOption.text=item;
              alert(result2);
            }, function (result) {alert(result)});
          },
          toastFunction(msg){
            this.$toast({
              message: msg,
              duration: 1000
            });
          },
          submit(epc){
            let $this=this;
            $this.$indicator.open();
            let Sepc=[...new Set(epc)];
            this.$http({
              method:'post',
              headers: {'Accept': '*/*'},
              url:'/api/ToolClient/QueryLendList',
              data:{
                CabinetID:localStorage.getItem("codeId"),
                epcs:Sepc,
                UserID:localStorage.getItem("UserID"),
                Name:localStorage.getItem("Name")
              }
            }).then(function(res){
              $this.$indicator.close();
              // if(res.data.Result===200){
              //   $this.items=res.data.Data;
              // }
              console.log(res);
            }).catch(function(err){
              console.log(err)
            })
          }
        }
    }
</script>

<style scoped>
  @import "../../static/icon/iconfont.css";
  .tab{
    width: 100%;
    border-collapse: collapse;
    position: relative;
    z-index: 100;
  }
  .tab tr td{
    border: 1px solid #ccc;
    font-size: 1.2em;
  }
  .tab tr td button{
    font-size: 1.3rem;
    width: 98%;
    padding-top: 5px;
    padding-bottom: 10px;
  }
  .tab thead td{
    border: 1px solid white;
    font-size: 1.2em;
    font-weight: bold;
    background: #ccc;
  }
  br{
    clear: both;
  }
  .main{
    height: 100%;
    width: 100%;
    background: white;
    position: relative;
    margin-top: 5px;
    z-index: 0;
  }
  .icon-sousuo{
    margin-top: -5px;
  }
</style>



