<template>
  <div>
    <table>
      <tr>
        <td>
          <button class="Button" @click="Toguizi()">
            <span class="iconfont icon-guizi"></span><br />
            {{btn1}}
          </button>
        </td>
        <td>
          <button class="Button" @click="TozcTool()">
            <span class="iconfont icon-zichan"></span><br />
            {{btn2}}
          </button>
        </td>
      </tr>
      <tr>
        <td>
          <button class="Button" @click="ToyjTool()">
            <span class="iconfont icon-tool"></span><br />
            {{btn3}}
          </button>
        </td>
        <td>
          <button class="Button" @click="Toyjreturn()">
            <span class="iconfont icon-guihuan"></span><br />
            {{btn4}}
          </button>
        </td>
      </tr>
    </table>
    <button class="Button" :style="retu" @click="routerback()">{{b2}}</button>
  </div>
</template>

<script>
  import { realconsole } from '../../static/test.js'
    export default {
        name: "index",
        data(){
          return {
            b2:"返回",
            btn1: "按柜子借用",
            btn2: "按资产借用",
            btn3: "一 键 借 用",
            btn4: "一 键 归 还",
            retu:{
              position: "absolute",
                bottom: "3%",
                right: "3%"
            },
          }
        },
      mounted:function() {
        this.$indicator.close();
        realconsole();
      },
      methods:{
        routerback(){
          if (this.$route.query.goindex === 'true') {
            this.$router.push('/')
          } else {
            this.$router.back(-1)
          }
        },
        Toguizi() {
          this.$router.push({name: 'Guizi'})
        },
        TozcTool(){
          this.$router.push({name:'zcTool'})
        },
        ToyjTool(){
          this.$router.push({name:'yjTool'})
        },
        Toyjreturn(){
          this.$router.push({name:'yjreturn'});


        }
      }
    }
</script>

<style scoped>
  @import "../../static/icon/iconfont.css";
  table{
    width: 60%;
    text-align: center;
    margin: 10% auto;
  }
  table button{
    margin-bottom: 20px;
    width: 95%;
  }
  .iconfont{
    font-size: 50px;
  }

</style>



