<template>
<div class="yjreturn">
  <input :style="inSty" v-model="Name"/>
  <button :style="btnSty" class="iconfont icon-sousuo" @click="search()" ></button>
  <br/>
  <div class="main">
    <table>
      <thead>
      <td>&nbsp;&nbsp;</td>
      <td>操作</td>
      <td>借用资产</td>
      <td>型号</td>
      <td>货位号</td>
      <td>EPC号</td>
      </thead>
      <tr v-for="(item,index) in items">
        <td>&nbsp;&nbsp;{{index+1}}&nbsp;&nbsp;</td>
        <td><button class="Button" @click="tool(item)">归还</button></td>
        <td>{{item.ToolName}}</td>
        <td>{{item.Spec}}</td>
        <td>{{item.LocationCode}}</td>
        <td>{{item.EPC}}</td>
      </tr>
    </table>
  </div>
  <v-dialog v-show="showDialog" :dialog-option="dialogOption" ref="dialog"></v-dialog>
  <button :style="retu" @click="routerback()">{{b2}}</button>
</div>
</template>

<script>
  import dialog from "./dialog"
    export default {
      name: "yjreturn",
      data(){
        return{
          b2:"返回",
          retu:{
            position: "absolute",
            bottom: "3%",
            right: "3%",
            padding:" 6px 40px",
            fontSize:"40px"
          },
          inSty:{
            position:"relative",
            width:"90%",
            float:"left",
            marginTop:"-500",
            height:"30px"
          },
          btnSty:{
            width:"8%",
            float:"right",
            marginRight:"1%",
            padding:" 10px 5px",
            position: "relative"
          },
          cz:{
            backgroundColor:"#EDEDED"
          },
          Name:null,
          items:[],
          showDialog:"",
          dialogOption:{
            title:"",
            text:"",
            cancelButtonText:"再次盘库"
          }
        }
      },
      components:{
        'v-dialog': dialog,
      },
      mounted:function () {
        let Url=localStorage.getItem("Url");
        let $this=this;
        this.$indicator.open({
          text: 'Loading...',
          spinnerType: 'fading-circle'
        });
        this.$http({
          method:'post',
          headers: {'Accept': '*/*'},
          url:Url+'/api/ToolClient/QueryListBack',
          data:{
            UserID:localStorage.getItem("UserID"),
            MainCabinetID:localStorage.getItem("codeId")
          }
        }).then(function(res){
          $this.$indicator.close();
          if(res.data.Result===200){
            $this.items=res.data.Data;
          }
          console.log(res);
        }).catch(function(err){
          console.log(err)
        })
      },
      methods:{
        routerback(){
          if (this.$route.query.goindex === 'true') {
            this.$router.push('/')
          } else {
            this.$router.back(-1)
          }
        },
        Return(item){
          let $this=this;
          let Url=localStorage.getItem("Url");
          console.log(item);
          $this.dialogOption.text=item;
          this.showDialog = true;
          this.$refs.dialog.confirm().then(() => {
            this.showDialog = false;
            // 再次盘库
          }).catch(() => {//确定
            this.showDialog = false;
            //   $this.$indicator.open({
            //     text: 'Loading...',
            //     spinnerType: 'fading-circle'
            //   });
            //   $this.$http({
            //     method:'post',
            //     headers: {'Accept': '*/*'},
            //     url:'/api/ToolClient/QueryLendList',
            //     data:{
            //       CabinetID:localStorage.getItem("codeId"),
            //       epcs:item.Spec,
            //       UserID:localStorage.getItem("UserID"),
            //       Name:localStorage.getItem("Name")
            //     }
            //   }).then(function(res){
            //     $this.$indicator.close();
            //     // if(res.data.Result===200){
            //     //   $this.items=res.data.Data;
            //     // }
            //     console.log(res);
            //   }).catch(function(err){
            //     console.log(err)
            //   })
            // next();
          })
          // .confirm({
          //   title: 'Notice',
          //   message: 'Are you sure?',
          //   showCancelButton: true,
          //   cancelButtonText:"重新盘库"
          // });
          // $this.$messagebox.confirm(html).then(_ => {

          // alert("asfas")

          // }).catch(_ => {

          // alert("45165")

          // })
          // if(item.Count>0){

          // }else{
          //   $this.$toast({
          //     message: '库存为空',
          //     position: 'bottom',
          //     duration: 1000
          //   });
          // }
        },
        search(){
          let $this=this;
          let Url=localStorage.getItem("Url");
          $this.$indicator.open({
              text: 'Loading...',
              spinnerType: 'fading-circle'
            });
            $this.$http({
              method:'post',
              headers: {'Accept': '*/*'},
              url:Url+'/api/ToolClient/QueryListBackByName',
              data:{
                MainCabinetID:localStorage.getItem("codeId"),
                UserID:localStorage.getItem("UserID"),
                Name:$this.Name
              }
            }).then(function(res){
              $this.$indicator.close();
              if(res.data.Result===200){
                $this.items=res.data.Data;
              }
              console.log(res);
            }).catch(function(err){
              console.log(err)
            })
        }
      }
  }
</script>

<style scoped>
  @import "../../static/icon/iconfont.css";
  table{
    width: 100%;
    border-collapse: collapse;
    position: relative;
    z-index: 100;
  }
  table tr td{
    border: 1px solid #ccc;
    font-size: 1.2em;
  }
  table thead td{
    border: 1px solid white;
    font-size: 1.2em;
    font-weight: bold;
    background: #ccc;
  }
  br{
    clear: both;
  }
  .main{
    height: 400px;
    width: 100%;
    background: white;
    position: relative;
    margin-top: 5px;
    z-index: 0;
  }
  table tr td button{
    font-size: 1.3rem;
    width: 98%;
    padding-top: 5px;
    padding-bottom: 10px;
  }
</style>



