<template>
  <div class="gezi">
    <h2>{{msg}}</h2>
    <!--<router-link to="/jyzc">-->
      <button class="Button" v-for="item in items" :style="{marginLeft: Btnleft,padding:Pad}" @click="sub(item)">{{item.BoardName}}</button>
    <!--</router-link>-->
      <button :style="retu" class="Button" @click="routerback()">{{b2}}</button>

  </div>
</template>

<script>
    export default {
        name: "gezi",
        data(){
          return {
            msg:"请选择格子",
            b1:"A1",
            b2:"返回",
            retu:{
              position: "absolute",
              bottom: "3%",
              right: "3%"
            },
            Btnleft:"1em",
            Pad:"10px 20px",
            items:[]
          }
        },
        mounted:function () {
          let Url=localStorage.getItem("Url");
          console.log(this.$route.query);
          let $this=this;
          this.$indicator.open({
            text: 'Loading...',
            spinnerType: 'fading-circle'
          });
          this.$http({
            method:'post',
            headers: {'Accept': '*/*'},
            url:Url+'/api/ToolClient/QueryListBoardName',
            data:{
              ID:localStorage.getItem("codeId"),
              CabinetName:this.$route.query.CabinetName
            }
          }).then(function(res){
            $this.$indicator.close();
            if(res.data.Result===200){
              $this.items=res.data.Data;
            }
            console.log(res);
          }).catch(function(err){
            console.log(err)
          })
        },
        methods:{
          routerback(){
            if (this.$route.query.goindex === 'true') {
              this.$router.push('/')
            } else {
              this.$router.back(-1)
            }
          },
          sub(item){
            let data={
              CabinetName:this.$route.query.CabinetName,
              BoardName:item.BoardName
            };
            this.$router.push({path: '/jyzc/dataObj', query: {dataObj: data}})
          }
        }
    }
</script>


<style scoped>
  .gezi{
    text-align: left;
  }
  h2{
    margin-top: 3%;
    color: red;
    text-indent: 2em;
  }

</style>



