<template>
  <div class="zcTool">
    <input :style="inSty" v-model="ToolName"/>
    <button :style="btnSty" class="iconfont icon-sousuo Button"  @click="search()"></button>
    <br/>
    <div class="main">

    </div>
    <table>
      <thead>
      <td>&nbsp;&nbsp;</td>
      <td>操作</td>
      <td>借用资产</td>
      <td>型号</td>
      <td>货位号</td>
      <td>库存数</td>
      </thead>
      <tr v-for="(item,index) in items">
        <td>&nbsp;&nbsp;{{index+1}}&nbsp;&nbsp;</td>
        <td><button class="Button" @click="tool(item)">借用</button></td>
        <td>{{item.ToolName}}</td>
        <td>{{item.Spec}}</td>
        <td>{{item.LocationCode}}</td>
        <td>{{item.Count}}</td>
      </tr>
    </table>
    <button class="Button" :style="retu" @click="routerback()">{{b2}}</button>
    <v-dialog v-show="showDialog" :dialog-option="dialogOption" ref="dialog"></v-dialog>
  </div>
</template>

<script>
  import dialog from "./dialog"
    export default {
        name: "zcTool",
        data(){
          return{
            b2:"返回",
            retu:{
              position: "absolute",
              bottom: "3%",
              right: "3%",
              padding:" 6px 40px",
              fontSize:"40px"
            },
            ToolName:null,
            inSty:{
              position:"relative",
              width:"90%",
              float:"left",
              marginTop:"-500",
              height:"30px"
            },
            btnSty:{
              width:"8%",
              float:"right",
              marginRight:"1%",
              padding:" 10px 5px",
              position: "relative"
            },
            items:[],
            showDialog:"",
            dialogOption:{
              title:"",
              text:"",
              cancelButtonText:"再次盘库"
            }
          }
        },
        mounted:function () {
          let Url=localStorage.getItem("Url");
          let $this=this;
          this.$http({
            method:'post',
            headers: {'Accept': '*/*'},
            url:Url+'/api/ToolClient/QueryTotalByName',
            data:{
              ID:localStorage.getItem("codeId"),
              ToolName:""
            }
          }).then(function(res){
            $this.$indicator.close();
            if(res.data.Result===200){
              $this.items=res.data.Data;
            }
            console.log(res);
          }).catch(function(err){
            console.log(err)
          })
        },
        components:{
          'v-dialog': dialog,
        },
        methods:{
          routerback(){
            if (this.$route.query.goindex === 'true') {
              this.$router.push('/')
            } else {
              this.$router.back(-1)
            }
          },
          tool(item){
            this.$indicator.open();
            let $this=this;
            let dataObj=this.$route.query.dataObj;
            // setTimeout(()=>{
            plus.plugintest.PluginTestFunction("Html5","Plus","AsyncFunction",dataObj.BoardName, function( result ) {
              if(result[3]===1){
                plus.nativeUI.showWaiting( "等待中..." );
                plus.nativeUI.closeWaiting();
                plus.plugintest.CloseFunction("Html5","Plus","AsyncFunction","MultiArgument!", function( res ) {
                  item["SyCounts"]=res.length;
                  $this.dialogOption.text=item;
                  $this.$indicator.close();
                  $this.showDialog = true;
                  $this.$refs.dialog.confirm().then(() => {
                    $this.showDialog = false;//确定
                    $this.$options.methods.submit(res);
                  }).catch(() => {//再次盘库
                    $this.$options.methods.again(item);
                  })
                }, function (result) {alert(result)});
                // setTimeout(()=>{
                //   $this.$options.methods.Close();
                // },100)
                // plus.plugintest.CloseFunction("Html5","Plus","AsyncFunction","MultiArgument!", function( res ) {
                //   if(res[3]===4){
                //     $this.$indicator.open();
                //     plus.plugintest.CountingFunction("Html5", "Plus", "AsyncFunction", "MultiArgument!", function (result1) {
                //       alert(result1);
                //       // $this.$indicator.close();
                //       $this.$refs.dialog.confirm().then(() => {
                //         $this.showDialog = false;//确定
                //       }).catch(() => {//再次盘库
                //         $this.$indicator.open();
                //         plus.plugintest.CountingFunction("Html5", "Plus", "AsyncFunction", "MultiArgument!", function (result2) {
                //           $this.$indicator.close();
                //           alert(result2);
                //         }, function (result) {alert(result)});
                //       })
                //     }, function (result) {alert(result)});
                //   }
                // }, function(result){alert(result)});
                // function Toast(msg,duration){
                //   duration=isNaN(duration)?3000:duration;
                //   var m = document.createElement('div');
                //   m.innerHTML = msg;
                //   m.style.cssText="width: 60%;min-width: 150px;opacity: 0.7;height: 30px;color: rgb(255, 255, 255);line-height: 30px;text-align: center;border-radius: 5px;position: fixed;top: 40%;left: 20%;z-index: 999999;background: rgb(0, 0, 0);font-size: 12px;";
                //   document.body.appendChild(m);
                //   setTimeout(function() {
                //     var d = 0.5;
                //     m.style.webkitTransition = '-webkit-transform ' + d + 's ease-in, opacity ' + d + 's ease-in';
                //     m.style.opacity = '0';
                //     setTimeout(function() { document.body.removeChild(m) }, d * 1000);
                //     }, duration);
                // }

              }
              else if(result[3]===2){
                $this.$options.methods.toastFunction('柜门打开失败');
              }
            }, function(result){alert(result)});
          },
          search(){
            let Url=localStorage.getItem("Url");
            let $this=this;
            $this.$indicator.open({
              text: 'Loading...',
              spinnerType: 'fading-circle'
            });
            $this.$http({
              method:'post',
              headers: {'Accept': '*/*'},
              url:Url+'/api/ToolClient/QueryTotalByName',
              data:{
                ID:localStorage.getItem("codeId"),
                ToolName:$this.ToolName
              }
            }).then(function(res){
              $this.$indicator.close();
              if(res.data.Result===200){
                $this.items=res.data.Data;
              }
              console.log(res);
            }).catch(function(err){
              console.log(err)
            })
          },
          Close(){
            let $this=this;
            plus.plugintest.CloseFunction("Html5","Plus","AsyncFunction","MultiArgument!", function( res ) {
              $this.showDialog = true;
              $this.$refs.dialog.confirm().then(() => {
                $this.showDialog = false;//确定
              }).catch(() => {//再次盘库
                $this.$indicator.open();
              })
            }, function (result) {alert(result)});
          },
          Counting(){
            let $this=this;
            $this.showDialog = true;
            plus.plugintest.CountingFunction("Html5", "Plus", "AsyncFunction", "MultiArgument!", function (result1) {
              alert(result1);
            }, function (result) {alert(result)});
          },
          again(item){
            let $this=this;
            plus.plugintest.CountingFunction("Html5", "Plus", "AsyncFunction", "MultiArgument!", function (result2) {
              item["SyCounts"]=res.length;
              $this.dialogOption.text=item;
              alert(result2);
            }, function (result) {alert(result)});
          },
          toastFunction(msg){
            this.$toast({
              message: msg,
              duration: 1000
            });
          },
          submit(epc){
            let $this=this;
            $this.$indicator.open();
            let Sepc=[...new Set(epc)];
            this.$http({
              method:'post',
              headers: {'Accept': '*/*'},
              url:'/api/ToolClient/QueryLendList',
              data:{
                CabinetID:localStorage.getItem("codeId"),
                epcs:Sepc,
                UserID:localStorage.getItem("UserID"),
                Name:localStorage.getItem("Name")
              }
            }).then(function(res){
              $this.$indicator.close();
              // if(res.data.Result===200){
              //   $this.items=res.data.Data;
              // }
              console.log(res);
            }).catch(function(err){
              console.log(err)
            })
          }
        }
    }
</script>

<style scoped>
  @import "../../static/icon/iconfont.css";
  table{
    width: 100%;
    border-collapse: collapse;
    margin-top: -500px;
    position: relative;
    z-index: 100;
  }
  table tr td{
    border: 1px solid #ccc;
    font-size: 1.2em;
  }
  table thead td{
    border: 1px solid white;
    font-size: 1.2em;
    font-weight: bold;
    background: #ccc;
  }
  br{
    clear: both;
  }
  table tr td button{
    font-size: 1.3rem;
    width: 98%;
    padding-top: 5px;
    padding-bottom: 10px;
  }
  .main{
    height: 500px;
    width: 100%;
    background: white;
    position: relative;
    margin-top: 5px;
    z-index: 0;
  }
  .icon-sousuo{
    margin-top: -5px;
  }
</style>



