<template>
  <div id="app">
    <h1>{{ msg }}</h1>
    <hr />
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',
  data(){
    return {
      msg: "无人值守资产管理系统",
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
body{
  background-color: rgba(0,0,0,0.6);
}
h1{
  font-size: 30px;
  text-align: center;
  color: yellow;
  text-shadow: 3px 3px 3px white;
  margin-top: -20px;
}
hr{
  height: 3px;
  border: none;
  border-radius: 1.5px;
  background: -webkit-linear-gradient(left, white , yellow,#FFFACD , yellow, white); /* Safari 5.1 - 6.0 */
  background: -o-linear-gradient(right, white, yellow,#FFFACD , yellow, white); /* Opera 11.1 - 12.0 */
  background: -moz-linear-gradient(right, white, yellow,#FFFACD , yellow, white); /* Firefox 3.6 - 15 */
  background: linear-gradient(to right, white , yellow,#FFFACD , yellow, white); /* 标准的语法 */
}
.Button{
  display: inline-block;
  color: white;
  padding: 6px 12px 15px 12px;
  font-size: 40px;
  font-weight: normal;
  line-height: 1.42857143;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-image: none;
  border: 2px solid white;
  border-radius: 10px;
  background: -webkit-linear-gradient(#E0E0E0, #2B2B2B); /* Safari 5.1 - 6.0 */
  background: -o-linear-gradient(#E0E0E0, #2B2B2B); /* Opera 11.1 - 12.0 */
  background: -moz-linear-gradient(#E0E0E0, #2B2B2B); /* Firefox 3.6 - 15 */
  background: linear-gradient(#E0E0E0, #2B2B2B); /* 标准的语法 */
}
</style>



