<template>
  <div class="dialog">
    <div class="mask"></div>
    <div class="dialog-content">
      <!--<h3 class="title">{{ modal.title }}</h3>-->
      <table class='tc'>
        <tr>
          <td class='first'>借用资产 :</td>
          <td class='two'>{{modal.text.ToolName}}</td>
        </tr>
          <tr>
            <td class='first'>型&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;号 :</td>
            <td class='two'>{{modal.text.Spec}}</td>
          </tr>
          <tr>
          <td class='first'>货&nbsp;&nbsp;位&nbsp;&nbsp;号 :</td>
          <td class='two'>{{modal.text.LocationCode}}</td>
        </tr>
        <tr>
          <td class='first'>库&nbsp;&nbsp;存&nbsp;&nbsp;数 :</td>
          <td class='two'>{{modal.text.Count}}</td>
        </tr>
        <tr>
          <td class='first'>借用数量 :</td>
          <td class='two'>{{modal.text.Count-modal.text.SyCounts}}</td>
        </tr>
        <tr>
          <td class='first'>剩&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;余 :</td>
          <td class='two'>{{modal.text.SyCounts}}</td>
        </tr>
      </table>
      <div class="btn-group">
        <div class="btn Button" @click="cancel">{{ modal.cancelButtonText }}</div>
        <div class="btn Button" @click="submit">{{ modal.confirmButtonText }}</div>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
      name: "dialog",
      props: {
        dialogOption: Object
      },
      data() {
        return {
          resolve: '',
          reject: '',
          promise: '', // 保存promise对象
        }
      },
      computed: {
        modal: function() {
          let options = this.dialogOption;
          return {
            title: options.title || '提示',
            text: options.text,
            cancelButtonText: options.cancelButtonText ? options.cancelButtonText : '取消',
            confirmButtonText: options.confirmButtonText ? options.confirmButtonText : '确定',
          }
        }
      },
      methods: {
        //确定,将promise断定为完成态
        submit() {
          this.resolve('submit');
        },
        // 取消,将promise断定为reject状态
        cancel() {
          this.reject('cancel');
        },
        //显示confirm弹出,并创建promise对象，给父组件调用
        confirm() {
          this.promise = new Promise((resolve, reject) => {
            this.resolve = resolve;
            this.reject = reject;
          });
          return this.promise; //返回promise对象,给父级组件调用
        }
      }
    }
</script>

<style scoped>
  .dialog {
    position: relative;
  }
  .dialog-content {
    position: fixed;
    box-sizing: border-box;
    padding: 20px;
    width: 80%;
    min-height: 160px;
    left: 10%;
    top: 20%;
    transform: translate(-50%, -50%);
    border-radius: 5px;
    background: #fff;
    z-index: 50002;
  }
  .title {
    font-size: 16px;
    font-weight: 600;
    line-height: 30px;
    margin-top: -10px;
  }
  .text {
    font-size: 14px;
    line-height: 30px;
    color: #555;
  }
  .btn-group {
    /*display: flex;*/
    /*position: absolute;*/
    /*right: 0;*/
    margin-top: 30px;
    /*bottom: 10px;*/
    width: 100%;
  }
  .btn {
    font-size: 1rem;
    margin: 0 auto;
    padding-top: 15px;
    padding-right: 20px;
    padding-left: 20px;
  }
  .btn:last-child {
     margin-left: 40%;
     color: #76D49B;
  }
  .mask {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 50001;
    background: rgba(0,0,0,.5);
  }
  .tc{
    width: 60%;
    font-size: 1.5rem;
    margin: -10px auto;
  }
  .tc tr td{
    width: 50%;
    text-align: center;
  }
  .tc tr .first{
    text-align: right;
  }
  .tc tr .two{
    text-align: left;
    text-indent: 10px;
  }
</style>



