<template>
  <div class="mintUiBox">
    <mt-field placeholder="请输入用户名" v-model="username"></mt-field>
    <br/>
    <mt-field placeholder="请输入密码" type="password" v-model="password"></mt-field>
    <button class="Button" @click="sub">登    录</button>
  </div>
</template>

<script>
    export default {
      name: "login",
      data(){
        return{
          username:"",
          password:"",
        }
      },
      methods:{
        sub(){
          // localStorage.setItem("Url","");
          let $this=this;
          $this.$indicator.open();
          localStorage.setItem("Url","http://192.168.9.100:10003");
          let Url=localStorage.getItem("Url");
          if(this.username===""){
            $this.$indicator.close();
            $this.$toast({
              message: '用户名为空',
              position: 'bottom',
              duration: 2000
            });
            return;
          }
          if(this.password===""){
            $this.$indicator.close();
            $this.$toast({
              message: '密码为空',
              position: 'bottom',
              duration: 2000
            });
            return;
          }
          this.$http({
            method:'post',
            headers: {'Accept': '*/*'},
            url:Url+'/api/ToolClient/QueryMainCabinetID',
            data:{
              code:'1',
            }
          }).then(function(res){
            if(res.data.Data.Result===true){
              localStorage.setItem("codeId",res.data.Data.ID);
            }
            console.log(res);
          }).catch(function(err){
            console.log(err)
          });
          this.$http({
            method:'post',
            headers: {'Accept': '*/*'},
            url:Url+'/api/User/Login',
            data:{
              Account:this.username,
              Password:this.password
            }
          }).then(function(res){
            if(res.data.Data.Result===true){
              plus.plugintest.LinkFunction("Html5","Plus","AsyncFunction","MultiArgument!", function( result ) {
                localStorage.setItem("UserID",res.data.Data.UserID);
                localStorage.setItem("Name",res.data.Data.Name);
                $this.$router.push({name:'index'});
                $this.$indicator.close();
                $this.$toast({
                  message: '登录成功',
                  position: 'bottom',
                  duration: 1000
                });
              }, function(result){alert(result)});
            }else{
              $this.$indicator.close();
              $this.$toast({
                message: '账号密码错误',
                position: 'bottom',
                duration: 2000
              });
            }
            console.log(JSON.stringify(res));
            console.log(res)
          }).catch(function(err){
            $this.$indicator.close();
            console.log(err)
          });
        }
      }
    }
</script>

<style scoped>
  .mintUiBox{
    width: 60%;
    text-align: center;
    margin: 10% auto;
  }
  button{
    padding: 10px 20px;
    margin-top: 10%;
    font-size: 2rem;
  }
</style>



