import Vue from 'vue'
import Router from 'vue-router'
import login from '../components/login'
import index from '../components/index'
import Gezi from '../components/Gezi'
import Guizi from '../components/Guizi'
import jyzc from '../components/jyzc'
import zcTool from '../components/zcTool'
import yjTool from '../components/yjTool'
import yjreturn from '../components/yjreturn'
// import HelloWorld from '@/components/HelloWorld'

Vue.use(Router);

export default new Router({
  // mode: 'history',
  routes:[
    {
      //根目录
      path:'/index',
      name:"index",
      component:index,
    },
    {
      //根目录
      path:'/',
      name:"login",
      component:login,
    },
    {
      path:'/Gezi/CabinetName',
      name:"Gezi",
      component:Gezi
    },
    {
      path:'/Guizi',
      name:"Guizi",
      component:Guizi,
    },{
      path:"/zcTool",
      name:"zcTool",
      component:zcTool,
    },
    {
      path:"/jyzc/dataObj",
      name:"jyzc",
      component:jyzc,
    },
    {
      path:"/yjTool",
      name:"yjTool",
      component:yjTool,
    },
    {
      path:"/yjreturn",
      name:"yjreturn",
      component:yjreturn,
    }
  ]
})



